<template>
  <div class="container">
    <div class="searchs">
      <el-form :inline="true" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="搜索">
              <el-input
                v-model="searchs.keyword_search_value"
                placeholder="姓名/手机号"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
              <el-button type="success" @click="showPackVip"
                >新增批量红包</el-button
              >
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="title" label="标题" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="mobile" label="手机号" align="center" />
      <el-table-column prop="vip_name" label="vip等级" align="center" />
      <el-table-column prop="money" label="红包金额" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" align="center" />
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />

    <el-dialog title="添加红包" :visible.sync="is_show_pack_vip" width="500px">
      <el-form :model="pack">
        <el-form-item label="红包名称" :label-width="label_width">
          <el-input v-model="pack.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="红包金额" :label-width="label_width">
          <el-input
            v-model="pack.money"
            type="number"
            step="0.01"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="会员类型" :label-width="label_width">
          <el-select v-model="pack.vip" placeholder="请选择活动区域">
            <el-option
              :label="v.name"
              :value="v.id"
              v-for="(v, k) in vip_config"
              :key="k"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="is_show_pack_vip = false">取 消</el-button>
        <el-button type="primary" @click="subPack">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      count: 0,
      page_curren: 1,
      searchs: {
        keyword_search_value: '',
      },
      tableData: [],
      loading: false,
      label_width: '80px',
      is_show_pack_vip: false,
      pack: {
        name: '',
        money: '',
        vip: '',
      },
      vip_config: {},
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/fin/funds/search', {
        stype: 'pack_send',
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.config = r.data.config
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.vip_config = r.data.config.vip
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    showPackVip() {
      this.is_show_pack_vip = true
    },
    subPack() {
      this.loading = true
      Fetch('/fin/pack/apply', {
        ...this.pack,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.config = r.data.config
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.is_show_pack_vip = false
          this.FetchList()
        })
        .catch(() => (this.loading = false))
    },
  },
}
</script>
